* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
  background: var(--slate-100);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

:root {
  --lime: hsl(61, 70%, 52%);
  --red: hsl(4, 69%, 50%);
  --white: hsl(0, 0%, 100%);
  --slate-100: hsl(202, 86%, 94%);
  --slate-300: hsl(203, 41%, 72%);
  --slate-500: hsl(200, 26%, 54%);
  --slate-700: hsl(200, 24%, 40%);
  --slate-900: hsl(202, 55%, 16%);
}

:root {
  --font-family: "Plus Jakarta Sans", sans-serif;
}

h1,
h2 {
  font-size: 1.25rem;
  color: var(--slate-900);
}
@media (min-width: 40em) {
  h1,
  h2 {
    font-size: 1.5rem;
  }
}

.container {
  max-width: 57.5rem;
  background: var(--white);
  display: flex;
  padding: 1.5rem;
  flex-direction: column;
}
@media (min-width: 40em) {
  .container {
    flex-direction: row;
  }
}

.form__values__fieldset {
  border: none;
}
.form__values__fieldset__input-group__title {
  color: var(--slate-700);
}
.form__values__fieldset__input-group__input-container {
  border: 0.0625rem solid var(--slate-700);
  display: flex;
}
.form__values__fieldset__input-group__input-container:hover .form__values__fieldset__input-group__input-container__span, .form__values__fieldset__input-group__input-container:focus .form__values__fieldset__input-group__input-container__span {
  background: var(--lime);
}
.form__values__fieldset__input-group__input-container:hover, .form__values__fieldset__input-group__input-container:focus {
  border: 0.0625rem solid var(--lime);
}
.form__values__fieldset__input-group__input-container__span {
  background: var(--slate-300);
  font-size: 1.125rem;
  padding: 0.75rem 1rem;
  color: var(--slate-900);
}
.form__values__fieldset__input-group__input-container__input {
  border: none;
  font-size: 1.125rem;
  padding: 0.75rem 1rem;
  outline: none;
  flex: 1;
}
.form__values__submit-btn {
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1.125rem;
  border-radius: 2.625rem;
  border: none;
  background: var(--lime);
}
@media (min-width: 40em) {
  .form__values__submit-btn {
    width: fit-content;
  }
}
.form__type {
  border: none;
}
.form__type__title {
  color: var(--slate-700);
}
.form__type__input-group {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 0.0625rem solid var(--slate-700);
}
.form__type__input-group:hover, .form__type__input-group:focus {
  background: var(--lime) 0.3;
  border-color: var(--lime);
}
.form__type__input-group:hover .form__type__input-group__input, .form__type__input-group:focus .form__type__input-group__input {
  border-color: var(--lime);
}
.form__type__input-group__title {
  color: var(--slate-900);
  font-size: 1rem;
  font-weight: 900;
}

.header {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 40em) {
  .header {
    flex-direction: column;
    gap: 0.625rem;
  }
}
.header__title {
  font-size: 1.5rem;
  line-height: 1.2;
}
.header__clear-btn {
  font-size: 1rem;
  font-weight: 500;
  border: none;
  background: transparent;
  text-decoration: underline;
  color: var(--slate-700);
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}